	module.exports = {"data":{"markdownRemark":{"html":"<p>当時よりさらに約5年前、高校生だった時にひがし氏と構想していたゲーム。コンセプトは「便所ティック・シューティング」。 無名ヒーローの主人公は世界中の便所を支配しようとする宇宙外生命体ストーカー、イマムラーの野望を食い止めるべく自ら便所に入り立ち向かう……  というシナリオ。構想段階ではフル3Dの360°STGの予定だったが技術的な限界もあり2Dの縦STGとして制作開始。実際に暗中模索ながらもステージ1から2の途中、EXボスの弾幕パターンの一部は完成していた。構想は1～5ステージ、EXTRAステージが2つと言った感じで、弾幕デザインも一部出来ていたが、最終的に<a href=\"/danmaq_works\">当時依頼を受けて制作していたSTG</a>と<a href=\"/th06ph\">紅魔郷PH～月夜に魅せられた少女</a>辺りに吸収合併され制作中断。 このゲームを制作するにあたって YaneGameSDK を読み解いたのが、決定的なプログラミングスキル向上につながり、大きなゲームでもプログラミング設計が崩壊しないようになってきた。</p>\n<ul>\n<li>ジャンル: 縦型弾幕STG</li>\n<li>使用言語: VisualC++.NET 2003 + <a href=\"http://yanesdkdotnet.sourceforge.jp/\">YaneuraoGameSDK</a></li>\n<li>最終更新: 2004/2</li>\n</ul>","frontmatter":{"cover":null,"date":"2004-01-01T00:00:00+09:00","strDate":"2003/12/31","title":"REVENGER (旧称:裡便者)"}}},"pathContext":{"path":"/ja/blog/2004/01/01-revenger/","langKey":"ja"}}

